.search-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-top: 10px;
}

.search-input {
  padding: 8px 15px;
  width: 250px;
  font-size: 15px;
  border-width: 0px;
  background: #a592f2;
  color: #e7e9f0;
  border-radius: 50px;
  box-shadow: 0 0 5px rgb(255, 255, 255, 0.5);
}

.search-input::placeholder {
  color: #e7e9f0;
}

.search-input:focus {
  outline: none;
}
